import {Component, OnInit} from '@angular/core';
import {Apollo} from 'apollo-angular';
import {AuthService} from '../../../services/auth.service';
import {getUserProfile, updateStepMutation} from '../../auth/authMutation';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {sendjobRequest} from '../../home/home/homemutation';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.scss']
})
export class ProfileComponent implements OnInit {
  recUserData: any;
  userData: FormGroup = this.fb.group({name: [''], phone: [''], email: [''], company: ['']});
  disable = true;

  constructor(private apollo: Apollo, private auth: AuthService, private fb: FormBuilder) {
  }

  ngOnInit(): void {
    this.apollo.watchQuery({
      query: getUserProfile,
    }).valueChanges.subscribe((result: any) => {
      this.recUserData = result.data.me;
      this.createForm();
    });
  }

  createForm(): void {
    this.userData = this.fb.group({
      name: [this.recUserData.name, Validators.required],
      id: [this.recUserData.id, Validators.required],
      phone: [this.recUserData.phone, Validators.required],
      email: [this.recUserData.email, Validators.required],
      company: [this.recUserData.company, Validators.required]
    });
  }

  update(): void {
  }
}
