import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {EstimattionHistoryComponent} from './estimattion-history/estimattion-history.component';
import {ProfileComponent} from './profile/profile.component';
import {ProfileNavComponent} from './profile-nav/profile-nav.component';
import {RouterModule} from '@angular/router';
import {SharedModule} from '../../shared/shared.module';
import { DisableControlDirective } from './profile/disable-control.directive';

const routes = [
  {
    path: '', component: ProfileNavComponent,
    children: [
      {path: '', redirectTo: 'overview', pathMatch: 'full'},
      {path: 'history', component: EstimattionHistoryComponent},
      {path: 'overview', component: ProfileComponent},
    ],


  },

];

@NgModule({
  declarations: [EstimattionHistoryComponent, ProfileComponent, ProfileNavComponent],
  imports: [
    RouterModule.forChild(routes), SharedModule,
    CommonModule
  ]
})
export class ProfileModule {
}
