import {Component, OnInit} from '@angular/core';
import {getUserProfile, historyQuery} from '../../auth/authMutation';
import {Apollo} from 'apollo-angular';

@Component({
  selector: 'app-estimattion-history',
  templateUrl: './estimattion-history.component.html',
  styleUrls: ['./estimattion-history.component.scss']
})
export class EstimattionHistoryComponent implements OnInit {
  pricingHistory: any;

  constructor(private apollo: Apollo) {

  }

  ngOnInit(): void {
    this.apollo.watchQuery({
      query: getUserProfile,
    }).valueChanges.subscribe((result: any) => {
      this.apollo.watchQuery({
        query: historyQuery, fetchPolicy: 'network-only'
      }).valueChanges.subscribe((resulto: any) => {
        this.pricingHistory = resulto.data.findManyPricingHistories.entities;
      });

    });


  }

}
